"use client";
import { useState } from "react";
// import { Button } from "@/components/ui/button";
// import { Card, CardContent } from "@/components/ui/card";
import { Button } from "./comp/Button";
import { Card, CardContent } from "./comp/Card";

export default function SpamClassifierUI() {
  const [text, setText] = useState("");
  const [result, setResult] = useState(null);
  const [toggle, settoggle] = useState(false)

  const handleSave = () => {
    settoggle(true)

  }

  const handleClassify = () => {
    // Placeholder logic
    if (!text.trim()) return;
    const isSpam = text.toLowerCase().includes("win") || text.toLowerCase().includes("free");
    setResult(isSpam ? "Spam" : "Not Spam");
  };

  return (
    <div className="min-h-screen flex flex-col  bg-gray-50">

      <main className="flex-grow flex items-center justify-center p-6 mt-[-150] ">
        <div className="w-full max-w-7xl shadow-xl rounded-2xl bg-white rounded-2xl shadow p-2 pt-0 ">
          <CardContent className="p-6 space-y-4">
            <h2 className="text-2xl font-semibold text-center">Check if a message is spam</h2>
            <textarea
              className="w-full p-3 border rounded-xl focus:outline-none focus:ring"
              rows={5}
              placeholder="Enter message here..."
              value={text}
              onChange={(e) => setText(e.target.value)}
            />
            <Button className="w-full py-3 rounded-xl" onClick={handleClassify}>Classify</Button>
            {result && (
              <div className={`mt-4 text-center text-lg font-bold ${result === "Spam" ? "text-red-600" : "text-green-600"}`}>
                Result: {result}
              </div>
            )}
          </CardContent>
          <CardContent className="p-6 space-y-4">
            <h2 className="text-2xl font-semibold text-center">Add new spam message if you have</h2>
            <textarea
              className="w-full p-3 border rounded-xl focus:outline-none focus:ring"
              rows={5}
              placeholder="Enter message here..."
              value={text}
              onChange={(e) => setText(e.target.value)}
            />
            <Button className="w-full py-3 rounded-xl" onClick={handleSave}>Save</Button>
            {toggle && (
              <div className="mt-4 text-center text-lg font-bold">
                Saved
              </div>
            )}

          </CardContent>
        </div>
      </main>


    </div>
  );
}



